@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;400;700&display=swap);
.movies .movie {
  background-color: white;
  margin-bottom: 70px;
  display: grid;
  grid-template-columns: minmax(150px, 1fr) 2fr;
  grid-gap: 20px;
  /* align-items: flex-start;
  justify-content: space-between; */
  font-weight: 200;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  position: relative;
  top: -30px;
  max-width: 160px;
  margin-right: 30px;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 400;
}

.movie .movie__title {
  margin-bottom: 1px;
  font-size: 24px;
}

.movie .movie__year,
.movie .movie__genres {
  opacity: 0.6;
}

.movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.movie__genres li {
  margin-right: 8px;
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 200;
  font-size: 2em;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

@media screen and (max-width: 1090px) {
  .movies {
    grid-template-columns: 1fr;
    width: 100%;
  }
}

.about__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  width: 100%;
  max-height: 400px;
  font-weight: 200;
}

.about__container span:first-child {
  font-size: 20px;
}

.about__container span:last-child {
  display: block;
  margin-top: 10px;
}

body {
  font-size: 14px;
}

.detail {
  position: relative;
  /* left: 5vw;
  top: 2vh; */
  margin: 2vh 5vw;
}

.detail__image {
  margin-top: 3%;
  margin-left: 3%;
  width: 20vw;
  height: 50vh;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detail__title__info {
  display: flex;
  align-items: baseline;
  align-content: flex-start;
  margin: 5vh auto;
}

.detail__title__info span {
  margin-right: 1%;
}

.detail__title {
  font-size: 5em;
}

.detail__year {
  font-size: 2em;
}

.detail__genres {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 5px 0px;
}

.genres__genre {
  font-size: 1.2em;
  padding-left: 0.5vw;
  margin-right: 1vw;
}

.detail__summary {
  font-size: 1.5em;
}

.nav {
  z-index: 1;
  position: fixed;
  top: 50px;
  left: 10px;
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 10px 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  border-radius: 10%;
}

@media screen and (max-width: 1090px) {
  .nav {
    left: auto;
    left: initial;
    top: auto;
    top: initial;
    bottom: 0px;
    width: 100%;
  }
}

.nav a {
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
}

.nav a:not(:last-child) {
  margin-bottom: 10px;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #f8f0f0;
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  height: 100%;
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

